















@import '../../src/palette';

[class*="box-"] {
  display: inline-block;
  width: 100px;
  height: 100px;
  padding: 4px;
  margin: 8px;
  word-wrap: break-word;
  vertical-align: top;
  border-radius: 4px;
}

@each $name, $value in $palette {
  .box-#{$name} {
    color: get-contrast-color($value);
    background-color: $value;
  }
}
